interface FormProps {
  form: any;
  setForm?: any;
}

const OverviewForm = ({ form, setForm }: FormProps) => {
  const handleContextChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const value = e.currentTarget.value;
    if (form.contexts.includes(value)) {
      setForm({
        ...form,
        contexts: form.contexts.filter(
          (context: string) => context !== value
        )
      });
    } else {
      setForm({
        ...form,
        contexts: [...form.contexts, value]
      });
    }
  };

  const handleInputChange = (
    e:
      | React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
      | React.MouseEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const target = e.currentTarget;
    setForm({
      ...form,
      [target.name]: target.value
    });
  };

  return (
    <React.Fragment>
      <FormGroup>
        <Label htmlFor="name">Tittel/navn p√• tilbud</Label>
        <TextInput
          type="text"
          name="name"
          value={form.name}
          onChange={handleInputChange}
        />
      </FormGroup>
      <FormGroup>
        <Label htmlFor="description">Beskrivelse</Label>
        <TextArea
          name="description"
          value={form.description}
          onChange={handleInputChange}
          maxLength={400}
        />
      </FormGroup>
      <FormGroup>
        <Label htmlFor="moreInformationURL">URL/lenke til nettsted</Label>
        <TextInput
          type="text"
          name="moreInformationURL"
          value={form.moreInformationURL}
          onChange={handleInputChange}
        />
      </FormGroup>
      <FormGroup>
        <Label htmlFor="treatment">Tilbudet er prim√¶rt</Label>
        <RowContainer style={{ height: "48px" }}>
          <SegmentedButton
            value={TREATMENT.kompenserende}
            type="button"
            name="treatment"
            checked={form.treatment === TREATMENT.kompenserende}
            onClick={handleInputChange}
          />
          <SegmentedButton
            value={TREATMENT.behandlende}
            type="button"
            name="treatment"
            checked={form.treatment === TREATMENT.behandlende}
            onClick={handleInputChange}
          />
          <SegmentedButton
            value={TREATMENT.forebyggende}
            type="button"
            name="treatment"
            checked={form.treatment === TREATMENT.forebyggende}
            onClick={handleInputChange}
          />
        </RowContainer>
      </FormGroup>
      <FormGroup>
        <Label htmlFor="contexts">
          Tilbudet er relevant for f√∏lgende m√•l
        </Label>
        <ContextContainer>
          {contexts.map((context: any) => {
            return (
              <label
                key={context.id}
                style={{
                  display: "flex",
                  maxWidth: "320px",
                  marginBottom: "4px"
                }}
              >
                <input
                  type="checkbox"
                  name="contexts"
                  value={context.id}
                  checked={form.contexts.includes(context.id)}
                  onChange={handleContextChange}
                />
                {context.text}
              </label>
            );
          })}
        </ContextContainer>
      </FormGroup>
    </React.Fragment>
  );
};

const ObstacleForm = ({ form, setForm }: FormProps) => {
  const ContextContainerSmall = styled.div`
    padding: 4px;
    display: flex;
    background-color: #f1f1f1;
    font-style: italic;
    font-weight: 500;
    margin-bottom: 4px;
  `;

  const handleHindranceClick = (
    e: React.MouseEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const target = e.currentTarget;
    const newScore =
      target.value === "H√∏y" ? 2 : target.value === "Mild" ? 1 : 0;
    const targetName = target.name;
    const exists = form.functions.some(
      (functionObj: any) => functionObj.name === targetName
    );
    if (newScore === 0) {
      if (exists) {
        setForm({
          ...form,
          functions: form.functions.filter(
            (funcObj: any) => funcObj.name !== targetName
          )
        });
      }
    } else {
      if (exists) {
        setForm({
          ...form,
          functions: form.functions.map((funObj: any) => {
            if (funObj.name === targetName) {
              return { name: funObj.name, score: newScore };
            }
            return funObj;
          })
        });
      } else {
        setForm({
          ...form,
          functions: [
            ...form.functions,
            { name: targetName, score: newScore }
          ]
        });
      }
    }
  };

  const relevantFunctionLists: any[] = contexts
    .filter((context: any) => form.contexts.includes(context.id))
    .map((context: any) => context.hindrances);

  const relevantFunctions: string[] = [
    ...new Set(relevantFunctionLists[0].concat(...relevantFunctionLists))
  ] as string[];

  return (
    <React.Fragment>
      <FormGroup>
        <RowContainer>
          <span style={{ padding: "4px 0", whiteSpace: "nowrap" }}>
            <Label>Valgte m√•l:</Label>
          </span>
          <RowContainer style={{ flexWrap: "wrap" }}>
            {form.contexts.map((context: any) => {
              return (
                <ContextContainerSmall
                  style={{ marginLeft: "4px" }}
                  key={context}
                >
                  {context}
                </ContextContainerSmall>
              );
            })}
          </RowContainer>
        </RowContainer>
      </FormGroup>
      <FormGroup>
        <Label>
          Sett i forhold til m√•lene som er valgt, hvor relevant er
          tilbudet{" "}
          <span style={{ fontStyle: "italic" }}>{form.name}</span> for
          disse hindringene?
        </Label>
      </FormGroup>
      <FormGroup>
        <RowContainer>
          <Label style={{ width: "70%" }}>Hindring</Label>
          <Label style={{ width: "30%" }}>Relevans</Label>
        </RowContainer>
        <ColumnContainer>
          {relevantFunctions.map((functionId: string) => {
            return (
              <ContextContainerSmall
                key={functionId}
                style={{ alignItems: "baseline" }}
              >
                <Label style={{ width: "70%" }}>{functionId}</Label>
                <RowContainer style={{ height: "27px", width: "30%" }}>
                  <SegmentedButton
                    type="button"
                    name={functionId}
                    value="Ingen"
                    onClick={handleHindranceClick}
                    checked={
                      form.functions.some(
                        (funObj: any) =>
                          funObj.name === functionId && funObj.score === 0
                      ) ||
                      !form.functions.some(
                        (funObj: any) => funObj.name === functionId
                      )
                    }
                  />
                  <SegmentedButton
                    type="button"
                    name={functionId}
                    value="Mild"
                    onClick={handleHindranceClick}
                    checked={form.functions.some(
                      (funObj: any) =>
                        funObj.name === functionId && funObj.score === 1
                    )}
                  />
                  <SegmentedButton
                    type="button"
                    name={functionId}
                    value="H√∏y"
                    onClick={handleHindranceClick}
                    checked={form.functions.some(
                      (funObj: any) =>
                        funObj.name === functionId && funObj.score === 2
                    )}
                  />
                </RowContainer>
              </ContextContainerSmall>
            );
          })}
        </ColumnContainer>
      </FormGroup>
    </React.Fragment>
  );
};

const EditServicePage = (props: EditServiceProps) => {
  const { history, location } = props;
  const {
    addService,
    isEditing,
    activeService,
    setActiveService
  } = useContext(ServicesContext);
  const [form, setForm] = useState<Service>(
    isEditing
      ? activeService
      : {
          id: "",
          name: "",
          description: "",
          treatment: "",
          moreInformationURL: "",
          contexts: [],
          functions: []
        }
  );
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);

  const resetEditing = () => {
    setActiveService(false, null);
  };

  const validateForm = () => {
    let valid =
      form.name.length > 2 && form.contexts.length > 0 && form.treatment;
    return valid;
  };

  const handlePreviousClicked = () => {
    if (currentPage === 1) {
      resetEditing();
      history.push("/");
    }
    if (currentPage === 2) {
      setCurrentPage(1);
    }
  };

  const handleNextClicked = () => {
    if (currentPage === 1) {
      setCurrentPage(2);
    }
    if (currentPage === 2) {
      if (validateForm()) {
        const newService: Service = {
          id: form.name,
          ...form
        };
        addService(newService);
      }
      resetEditing();
      history.push("/");
    }
  };

  return (
    <ColumnContainer>
      <Title onClick={() => history.goBack()}>
        Tilbake til startsiden
      </Title>
      <Description>
        {form.name || isEditing
          ? `Rediger tilbud: ${form.name}`
          : "Opprett et nytt tilbud"}
      </Description>
      <FormContainer>
        {currentPage === 1 ? (
          <OverviewForm form={form} setForm={setForm} />
        ) : (
          <ObstacleForm form={form} setForm={setForm} />
        )}
        <ButtonRow>
          <ActionButton secondary onClick={handlePreviousClicked}>
            {currentPage === 1 ? "Avbryt" : "G√• til forrige steg"}
          </ActionButton>
          <ActionButton onClick={handleNextClicked}>
            {currentPage === 1
              ? "G√• til neste steg"
              : "Dette ser bra ut, lagre!"}
          </ActionButton>
        </ButtonRow>
      </FormContainer>
    </ColumnContainer>
  );
};

export default EditServicePage;
